.event-form {
    box-shadow: 1px 1px 5px #000;
    border-radius: 10px;
    padding: 30px 20px;
}

.plus__icon {
    width: 30px;
    height: 30px;
    color: #fff;
    background: var(--secondary);
    border-radius: 50%;
    padding: 5px;
    cursor: pointer;
    display: block;
    flex-basis: 33px;
}

.show-select {
    color: #fff;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;


    .show-item {
        padding: 10px;
        background: #333;
        font-weight: 600;
        cursor: pointer;
    }

    .active {
        background: var(--primary);
        color: #fff;
    }
}