.focus-none {
    box-shadow: none !important;
}

.suggest {
    margin-top: 10px;

    h6 {
        font-weight: 600;
    }

    .suggest-list {
        display: flex;
        flex-wrap: wrap;

        .suggest-link {
            background: #333;
            color: #fff;
            display: inline-block;
            padding: 2px 5px;
            border-radius: 5px;
            margin-right: 10px;
            margin-top: 8px;
            font-size: 13px;
        }
    }

}