.owl-carousel .nav-btn {
    height: 47px;
    position: absolute;
    width: 26px;
    cursor: pointer;
    top: 0px !important;
}

.event-wrapper {
    .owl-theme .owl-nav {
        margin-top: 10px;
        text-align: center;
        -webkit-tap-highlight-color: transparent;
        position: absolute;
        right: 70px;
        top: -60px;
        font-size: 17px;
        z-index: -1;
    }

}

.owl-carousel .owl-prev.disabled,
.owl-carousel .owl-next.disabled {
    pointer-events: none;
    opacity: 0.2;
}

.owl-theme .owl-nav [class*='owl-']:hover {
    background: transparent;
    color: var(--primary);
    text-decoration: none;
}



/********* responsive **********/
@media (max-width: 575.98px) {
    .videoItem__title {
        font-size: 15px;
        line-height: 1.5;
    }

    .owl-theme .owl-nav {
        right: 50px !important;
    }

    .owl-prev,
    .owl-next {
        height: 23px !important;
        width: 22px !important;
        color: #fff !important;
        line-height: 23px;

        svg {
            margin-top: -5px !important;
        }
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {

    .owl-theme .owl-nav {
        right: 50px;
        margin-top: 13px;
    }
}

