.coreValue__amount {}

.inventor__option {
    text-align: center;
}

.inventor__option h4 {
    font-weight: 600;
}

.coreValue__amount h2 {
    font-size: 18px;
}

.pdf-style {
    font-size: 40px;
    margin-top: 10px;
}

.dark {
    .pdf-style {
        color: #fff;
    }
}

.light {
    .pdf-style {
        color: #000;
    }
}

.coreValue__amount {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    background: #343844;
    text-align: center;
    position: relative;
    color: #fff;

    &:before {
        position: absolute;
        left: 0;
        bottom: 0;
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-color: var(--primary);
        transform-origin: 0 bottom 0;
        transform: scaleX(0);
        color: #fff;
        transition: .4s ease-out;
    }

    &:hover {
        .content {
            position: relative;

            h2,
            h4 {
                color: #FFF;
            }
        }

        &:before {
            transform: scaleX(1);
        }
    }
}

/****** Responsive *******/
@media (max-width: 575.98px) {
    .coreValue__amount {
        box-shadow: 1px 1px 5px -3px #000;
    }
}