.contact__img img {
    box-shadow: 1px 1px 5px #000;
    border-radius: 10px;
    background-color: #fff;
    height: 550px !important;
}
.contact__form__wrapper {
    box-shadow: 1px 1px 5px #000;
    border-radius: 10px;
    height: 100%;
    padding: 25px 20px;
}
.contact__form {
}
.contact__info h6 {
    font-weight: 600;
    position: relative;
    padding-bottom: 14px;
    margin-top: 30px;
}
.contact__info h6::before {
    content: '';
    position: absolute;
    height: 2px;
    background: var(--primary);
    top: 18px;
    left: 0;
    width: 50px;
}
.contact__info__option {
    display: flex;
    align-items: center;
    font-weight: 600;
}
.contact__info__option span {
    margin-left: 10px;
}
.contact__info .follow__icons {
    padding: 20px 0;
    font-size: 26px;
}
