.home__slider {
    position: relative;
    display: block;
    text-decoration: none;
}
.home__slider__content {
    position: absolute;
    top: 20%;
    left: 10%;
    color: #fff;
    width: 45%;
}
.home__slider__content h2,
h5 {
    font-weight: 600;
}
.home__slider__content p {
    font-size: 13px;
}
.buy__ticket__btn {
    border: none;
    background: #fff;
    padding: 4px 8px;
    border-radius: 5px;
    font-size: 14px;
    text-transform: uppercase;
    margin-top: 15px;
    color: var(--secondary);
    font-weight: 600;
    transition: all 0.3s;
}
.buy__ticket__btn:hover {
    color: #fff;
    background: var(--primary);
}

/******* search box hide *******/
.events__section .search-box {
    display: none;
}
.events__section .title__btn {
    padding: 7px 34px;
    flex-basis: 100%;
}
.events__section .title__button-1 {
    flex-basis: 100%;
}
.profile__section .search-box {
    display: none;
}
.profile__section .title__btn {
    padding: 7px 34px;
    flex-basis: 100%;
}
.profile__section .title__button-1 {
    flex-basis: 100%;
}
/******* Slick **********/

/********* Rating Hide *********/
.upcoming__section .event__item .event__review {
    display: none;
}

/********* subscribe *********/
.subscribe {
    box-shadow: 1px 1px 5px #444;
    padding: 15px 12px;
    border-radius: 8px;
}
.subscribe__form {
    display: flex;
    margin-bottom: 10px;
    flex-wrap: wrap;
}
.subscribe__form__input {
    width: 70%;
    height: 34px;
    border-radius: 5px;
    border: 2px solid var(--secondary);
    padding-left: 10px;
    padding-right: 5px;
    margin-right: 5px;
}
.subscribe__btn {
    border: none;
    background: var(--primary);
    color: #fff;
    font-size: 14px;
    width: 76px;
    border-radius: 5px;
    font-weight: 600;
}

.follow {
}
.follow__title {
}
.follow__icons {
    font-size: 20px;
}
.follow__icon {
    margin-right: 7px;
    transition: all 0.3s;
    color: var(--secondary);
}
.follow__icon:hover {
    color: var(--primary);
}

/***** Responsive **********/
@media (max-width: 575.98px) {
    .home__slider__content {
        position: absolute;
        top: 7%;
        left: 4%;
        color: #fff;
        width: 80%;
    }
    .home__slider__content h2 {
        font-size: 12px;
    }
    .home__slider__content h5 {
        font-size: 10px;
        margin-bottom: 2px;
    }
    .home__slider__content p {
        display: none;
    }
    .buy__ticket__btn {
        padding: 4px 8px;
        margin-top: 10px;
        font-size: 11px;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .home__slider__content {
        position: absolute;
        top: 6%;
        left: 7%;
        color: #fff;
        width: 65%;
    }
    .home__slider__content h2 {
        font-size: 21px;
    }
    .home__slider__content h5 {
        font-size: 15px;
    }
    .home__slider__content p {
        font-size: 11px;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .home__slider__content {
        position: absolute;
        top: 10%;
        left: 5%;
        width: 65%;
    }
}

@media (min-width: 992px) and (max-width: 1199.98px) {
    .home__slider__content {
        width: 55%;
    }
}
