.benefits-title {
    text-align: center;
    text-transform: uppercase;
    color: var(--primary);
    margin-top: 10px;
    margin-bottom: 15px;
    font-weight: 700;
}

.benefits {
    display: flex;
    justify-content: space-around;
    align-items: center;

}

.benefits2 {
    display: flex;
    justify-content: space-around;
    align-items: center;

}

.benefits-item {
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .benefits-icon {
        height: 70px;
        width: 70px;
        border-radius: 50%;
        background: var(--primary);
        margin-bottom: 8px;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            font-size: 40px;
        }
    }

    p {
        text-align: center !important;
    }
}


.artist-wrapper {
    .accordion__button {
        background-color: #f4f4f4;
        color: #444;
        cursor: pointer;
        padding: 8px;
        width: 100%;
        text-align: left;
        border: none;
        border-radius: 5px;
    }
}

.success {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    .success-content {
        display: flex;
        align-items: center;

        .icon {
            font-size: 40px;
            color: green;
            margin-right: 5px;
        }

        .message {

            h2 {
                font-weight: 700;
            }
        }
    }

}