.voucher__card__title {
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 10px;
}

.voucher__card__img {
    position: relative;

    img {
        box-shadow: var(--shadow);
        transition: all 0.6s;
        background: #fff;
        border-radius: 5px;
        width: 100%;

        &:hover {
            transform: scale(1.05);
            box-shadow: 0px 0px 4px 3px #efe9e9;
        }
    }

    button {
        position: absolute;
        height: 100%;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: none;
    }

    &:hover {
        button {
            display: block;
        }
    }
}

/********* responsive **********/
@media (max-width: 575.98px) {}

@media (min-width: 576px) and (max-width: 767.98px) {}

@media (min-width: 768px) and (max-width: 991.98px) {
    .voucher__card__title {
        font-size: 14px;
    }
}

@media (min-width: 992px) and (max-width: 1199.98px) {}

