.navbar {
    display: flex;
    align-items: center;
    text-align: center;
    color: #fff;
    padding: 0;
    width: 100%;
    height: 60px;
    position: fixed;
    z-index: 100;
    top: 0px;
    vertical-align: middle;
    transition: background-color 0.5s linear 0s;
    background-image: linear-gradient(0deg, rgba(51, 51, 51, 0) 0%, rgba(51, 51, 51, 0.75) 100%);

    .navbar-brand img {
        width: 135px;
        object-fit: contain;
    }

    .active {
        border-bottom: 1px solid red;
        font-weight: 600;
    }

    .nav-link {
        margin: 0 7px;
        padding: 3px;
        text-transform: capitalize;
        color: rgb(255, 255, 255) !important;
        opacity: 0.8;
        font-size: 16px;
        position: relative;

        &::before {
            content: "";
            width: calc(0%);
            position: absolute;
            top: 100%;
            left: 0;
            height: 2px;
            background-color: var(--primary);
            transition: all 0.4s linear;
        }

        &:hover {
            color: #fff;
            opacity: 1;

            &::before {
                width: calc(100%);
            }
        }


    }

    @media (min-width: 1200px) {

        .container,
        .container-lg,
        .container-md,
        .container-sm,
        .container-xl,
        .container-xxl {
            max-width: 95% !important;
        }
    }
}

.navbar-toggler {
    border-radius: 2px;
}

.navbar-light .navbar-toggler {
    color: red;
    font-size: 23px;
    border: none;

    &:focus {
        box-shadow: none;
    }
}

body.dark {

    .fixed2,
    .fixed {
        box-shadow: var(--shadow-dark);
    }
}

.fixed {
    background: rgb(10, 12, 15);
    position: fixed !important;
}

.fixed2 {
    background: rgb(10, 12, 15);
    position: fixed;
}


.searchBoxWrapper {
    width: 264px;
    cursor: pointer;

    .searchBox {
        position: relative;
        display: flex;
        width: 100%;
        height: 36px;
        border-radius: 4px;
        box-sizing: border-box;
        z-index: 1;

        .searchForm {
            width: 100%;
            position: relative;

            input {
                width: 264px;
                height: 36px;
                padding: 0px 70px 0px 15px;
                box-sizing: border-box;
                border-radius: 4px;
                display: block;
                background: rgba(255, 255, 255, 0.15);
                border: 1px solid rgba(255, 255, 255, 0.15);
                color: rgb(255, 255, 255);
                font-size: 14px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                outline: none;

                &::placeholder {
                    color: #f5f5f5;
                }
            }

            .searchBtn {
                position: absolute;
                right: 11px;
                top: 5px;
                width: 22px;
                height: 24px;
                display: flex;
                -webkit-box-align: center;
                align-items: center;
                -webkit-box-pack: center;
                justify-content: center;
                color: #fff;

                svg {
                    width: 22px;
                    height: 20px;
                    margin-top: 2px;
                }

                .line {
                    position: absolute;
                    right: 30px;
                    top: 6px;
                    width: 1px;
                    height: 16px;
                    background: rgba(255, 255, 255, 0.1);
                }
            }
        }
    }
}

.nav-item {
    padding: 10px 0;
    position: relative;

    .downIcon {
        font-size: 14px;
        transition: all 0.3s;
    }

    &:hover {
        .subMenuBox {
            visibility: visible;
            top: 50px;
        }

        .downIcon {
            transform: rotate(180deg);
        }
    }
}

/********* Nav Item Container **********/
.navLinkContainer {
    position: relative;
    margin-left: 16px;
    cursor: pointer;

    .iconWrapper {
        display: flex;
        box-sizing: border-box;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
        outline: none;
        background: rgba(0, 0, 0, 0.2);
        width: 36px;
        height: 36px;
        border: 1px solid rgba(255, 255, 255, 0.25);
        border-radius: 50%;


        svg {
            width: 21px;
            height: 21px;
        }
    }

    &:hover {
        .iconWrapper {
            background: rgba(255, 255, 255, 0.2);
        }

    }
}

/************* SubMenu ************/
.subMenuBox {
    position: absolute;
    left: 50%;
    top: 60px;
    transform: translate(-50%, 0px);
    visibility: hidden;
    transition: top 0.3s;


    .subMenuBoxContainer {
        position: relative;
        border: 1px solid rgb(102, 102, 102);
        border-radius: 4px;
        background-color: rgb(26, 28, 34);

        &::before {
            display: block;
            box-sizing: border-box;
            content: "";
            height: 12px;
            width: 12px;
            position: absolute;
            top: -1px;
            left: 50%;
            transform: translate(-50%, -50%) rotate(45deg);
            background-color: rgb(26, 28, 34);
            border-left: 1px solid rgb(102, 102, 102);
            border-top: 1px solid rgb(102, 102, 102);
            border-radius: 4px 0px 0px;
        }

        .subMenuBoxListWrapper {
            background: rgb(26, 28, 34);
            text-align: center;
            border-radius: 4px;

            >div {
                border-radius: 4px;
            }

        }
    }
}


.languageList {
    li {
        width: 144px;
        position: relative;
        height: 39px;
        line-height: 39px;
        font-size: 14px;
        cursor: pointer;
        text-align: center;

        &:hover {
            background: rgba(255, 255, 255, 0.06);
            font-weight: 500;
        }
    }

    li.languageActive {
        color: red;
        font-weight: 500;
    }
}

.vipTag {
    width: 101px;
    margin-left: 16px;
    margin-right: 10px;

    .userLevelTag {
        display: block;
        width: 100px;
        height: 36px;
        border-radius: 4px;
        background: var(--primary);
        line-height: 36px;
        font-weight: 600;
        font-size: 14px;
        transition: all 0.3s;


        &:hover {
            color: #fff;
            transform: scale(1.05);
        }

        svg {
            font-size: 19px;
            margin-right: 4px;
            margin-top: -3px;
        }
    }
}

.contentBox {
    display: flex;
    border-radius: 4px;
    height: 412px;
    width: 320px;
    top: 64px;
    background-color: #1a1c22;
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
    flex-direction: column;

    .tabs {
        position: relative;
        box-sizing: border-box;
        height: 48px;
        padding: 0px 12px;
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
        border-bottom: 1px solid rgb(55, 56, 61);

        .tabsItem {
            color: red;
            font-weight: 500;

            svg {
                font-size: 18px;
            }
        }
    }

    .favoriteList {
        height: 100%;
        padding-top: 8px;

        .item-list {
            display: flex;
            padding: 8px 12px;

            .img-box {
                width: 104px;
                height: 59px;
                border-radius: 4px;

                img {

                    height: 100%;
                    width: 100%;
                    border-radius: 4px;

                }
            }

            .content {
                flex: 1 1 0%;
                padding: 4px 0px 4px 8px;
                display: flex;
                flex-direction: column;
                -webkit-box-pack: justify;
                justify-content: space-between;

                .name {
                    font-size: 14px;
                    color: rgb(169, 169, 172);
                    letter-spacing: 0px;
                    line-height: 18px;

                    p {
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                    }
                }

                .schedule {
                    display: flex;
                    -webkit-box-align: center;
                    align-items: center;

                    .schedule-strip {
                        background: rgba(255, 255, 255, 0.12);
                        flex: 1 1 0%;
                        margin-right: 4px;
                        height: 2px;

                        .schedule-length {
                            height: 100%;
                            background: rgb(255, 255, 255);
                        }
                    }

                    &-number {
                        width: 32px;
                        font-size: 11px;
                        color: rgb(130, 131, 135);
                        letter-spacing: 0px;
                        text-align: right;
                        line-height: 13px;
                    }
                }
            }

            &:hover {
                background: rgba(255, 255, 255, 0.06);

                .name {
                    color: #fff;
                }
            }
        }
    }
}

.loginBox {
    width: 200px;
    background: rgb(26, 28, 34);
    border-radius: 4px;
    padding: 10px;

    p {
        line-height: 18px;
        font-size: 13px;
        color: rgb(130, 131, 135);
        margin: 0;
        padding: 0;
    }

    .loginBtn {
        display: inline-block;
        min-width: 88px;
        padding: 0px 12px;
        background: red;
        border-radius: 4px;
        line-height: 30px;
        font-size: 13px;
        height: 30px;
        box-sizing: border-box;
        margin-top: 16px;
        outline: none;
        border: none;
        color: #fff;
    }
}

/********** Responsive **********/
@media (max-width: 575.98px) {
    .navbar {
        .navbar-collapse {
            padding: 5px 0;
        }

        .nav-link {
            padding-bottom: 5px;
        }

        .active {
            border: none;
        }
    }

    .navbar {
        height: 50px;
    }

    .navbar-light .navbar-toggler {
        margin-right: 5px;
        margin-top: -4px;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .navbar {
        .navbar-collapse {
            padding: 10px 0;
        }

        .nav-link {
            padding-bottom: 5px;
        }

        .active {
            border: none;
        }
    }

    .navbar-light .navbar-toggler {
        margin-right: 5px;
        margin-top: -4px;
    }
}

