body.dark {
    .ReactModal__Content {
        background: var(--secondary) !important;

        .vip-item {
            background-color: #212121;
        }

        .selectedItem {
            border: 4px solid var(--primary);

            .vip-item {
                background-color: #1f1f1f;
            }

        }

        .custom__btn {
            background-color: var(--primary);
        }
    }
}

body.light {
    .ReactModal__Content {
        .vipItem {
            border-color: #f3f3f3;

            .vip-item {
                background-color: #f3f3f3;
            }
        }

        .selectedItem {
            border: 4px solid var(--primary);

            .vip-item {
                background-color: #f6f6f6;
            }

        }


    }
}

.vip-item {
    border-radius: 10px;
    padding: 37px 25px;
    font-size: 15px;

}

.auto__renew {
    padding: 10px 0;
}


.show__item {
    display: flex;
    font-size: 13px;
    margin-bottom: 14px;

    svg {
        font-size: 20px;
    }

    p {
        margin-left: 10px;
    }
}


.subs {
    font-weight: 600;
    line-height: 2;
}

.price .one {
    font-size: 28px;
}

.price .two {
    font-size: 15px;
    text-decoration: line-through;
    display: inline-block;
    margin-left: 5px;
}

.vip-item>p {
    padding-bottom: 25px;
}

.selectedItem {
    border: 4px solid var(--primary);
    cursor: pointer;
    border-radius: 14px;
}

.ReactModal__Content input {
    display: none;
}

label {
    display: block;
}