.ticketForm {
    border-radius: 10px;
    box-shadow: 1px 1px 5px #000;
    padding: 20px;
    height: 480px;
}

.payment__method {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.payment__method__img {
    flex-basis: 55px;
}

.payment__method__img img {
    width: 38px;
}

.payment__method__name {
    line-height: 10px;
    margin-left: 10px;
    flex-basis: 100%;
}

.payment__method__name h4 {
    text-transform: uppercase;
    margin-bottom: 0;
    font-weight: 700;
    font-size: 20px;
}

.payment__method__name span {
    font-size: 11px;
}

.payment__method__down {
    height: 35px;
    width: 35px;
    background: var(--secondary);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    min-width: 35px;
    margin-left: 5px;
}



/********* responsive **********/
@media (max-width: 575.98px) {
    .push-bottom {
        padding-bottom: 100px;
    }

}

@media (min-width: 576px) and (max-width: 767.98px) {
    .push-bottom {
        padding-bottom: 100px;
    }

}

