/********* Event Description **********/
.event__description__img {
    >div {
        background: transparent !important;
    }

    img {
        border: 1px solid var(--secondary);
        border-radius: 10px;
        width: 100% !important;
    }
}

.event__description {
    padding: 15px 20px;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 500;
}

.event__description__option {
    padding-bottom: 25px;
}

.event__description__option h5 {
    font-weight: 700;
}

.event-icons {
    margin-top: 16px;

    .footer__social__wrapper {
        justify-content: center;
    }
}


/******** Event Price *********/
.event-price {
    padding: 30px 15px;
    border-radius: 10px;
    display: flex;
    height: 84%;
}

.price__table {
    flex-basis: 50%;
}

.event__date {
    overflow-x: hidden;

    table tr th {
        padding-bottom: 7px;
        padding-left: 5px;
        min-width: 120px;
    }

    table tr td {
        padding-left: 10px;
        font-weight: 600;
        font-size: 13px;
        padding-bottom: 3px;
        min-width: 120px;
    }

}


.vertical__line {
    position: relative;
    margin: 0px 40px;
}

.vertical__line::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 2px;
    top: 0;
    left: 0;
    background: var(--secondary);
}


.event__date,
.event-price {
    table tr {
        border-bottom: 1px solid #e0dfdf;
    }

}

body.dark {

    .event__date,
    .event-price {
        table tr {
            border-bottom: 1px solid #313131 !important;
        }

    }

}

.event__date {
    table {
        tbody {
            tr {
                &:first-child {
                    th {
                        padding-top: 11px;
                        margin-bottom: 0;
                    }

                }
            }
        }
    }
}

.event__date,
.event-price {
    table {
        width: 100%;

        tbody {
            text-align: center;

            tr {

                &:first-child {
                    background: #d6d6d6 !important;
                    color: var(--secondary) !important;
                }

                &:hover {
                    background: var(--primary);
                    color: #fff;
                }

                th,
                td {
                    padding-left: 5px;
                    padding-right: 0;
                }

            }
        }
    }
}



.event-price table {
    width: 100%;
}

.event-price div {
    position: relative;
    margin: 0px 15px;
}

.event-price {
    div {
        &::before {
            content: '';
            position: absolute;
            height: 100%;
            width: 2px;
            top: 0;
            left: 0;
            background: #979797;
        }
    }
}

.comment-mt-10 {
    margin-top: 10px;
}



/************* Event Date **************/
.event__date {
    padding: 28px 30px;
    border-radius: 10px;
}

.date__table {
    width: 100%;

    td,
    td {
        min-width: 150px;
    }
}

/********* Icons **********/
.event__details__icons .follow__icons {
    font-size: 25px;
}

.event__details__icons .follow__icon {
    margin-right: 15px;
}

.event__details__icons {
    padding-top: 15px;
    padding-bottom: 20px;
    text-align: center;
}

/************ Responsive ********/
@media (max-width: 575.98px) {
    .event-price {
        padding: 25px 25px;
        display: block;
    }

    .price__table {
        flex-basis: 50%;
        width: 100%;
        margin-bottom: 28px;
    }

    .event__date {
        padding: 25px 10px;
        overflow-x: scroll;
    }

    .wrapping-event-title {
        .title {
            display: block !important;

            .title__right {
                justify-content: center;
                margin-top: 10px;
            }
        }
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .event__date {
        padding: 25px 15px;
        overflow-x: scroll;
    }

}

