.video-container {
    position: relative;
}

.feature-img {
    position: relative;
    border-radius: 6px;
    margin: auto;
    overflow: hidden;
}

/* .feature-img img {
    width: 100%;
} */
.play__btn {
    width: 60px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

.video-player {
    position: relative;
    left: 0;
    top: 0;
    cursor: pointer;
    display: none;
}


.close__btn {
    position: absolute;
    top: 10px;
    right: 140px;
    width: 100px;
    cursor: pointer;
    object-fit: cover;
}

.video {
    width: 100%;
    height: 600px !important;
}

.video iframe {
    height: 600px !important;
}

video:focus {
    outline: none;
}

iframe {
    width: 100%;
}


/****** Responsive ******/

@media (max-width: 575.98px) {
    .video {
        height: 100% !important;

        iframe {
            height: 100% !important;
        }
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .video {
        height: 100% !important;

        iframe {
            height: 100% !important;
        }
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .video {
        height: 400px !important;

        iframe {
            height: 400px !important;
        }
    }
}

