:root {
    --primary: #c9151b;
    --secondary: #2d2d2d;
    --shadow: rgb(149 157 165 / 20%) 0px 8px 24px;
    --shadow-dark: rgb(0 0 0 / 79%) 0px 8px 24px;
}

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}




body {
    overflow-x: hidden;
    height: 100%;
    font-family: sans-serif;

    &::-webkit-scrollbar {
        width: 10px;
        background: transparent;
    }


    &::-webkit-scrollbar-track {
        background: #21232b;
        box-shadow: inset 0 0 6px rgb(0 0 0 / 40%);
    }

    &::-webkit-scrollbar-thumb {
        background: $primary;
    }
}



body.dark {
    background: $dark-bg;
    color: #e2e2e2;

    a {
        color: rgb(255, 255, 255);
    }

}

body.light {
    background: $light-bg;
    color: #333;

    a {
        color: #333;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: sans-serif;
    margin-bottom: 0;
}

p {
    text-align: justify;
    margin-bottom: 0;
}



img {
    max-width: 100%;
    height: auto;
}

ul,
ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

a,
a:link,
a:visited,
a:hover,
a:active {
    text-decoration: none;
    user-select: none;
}