body.dark {
    .news__item {
        background: #21232b;
        color: #fff;
        box-shadow: var(--shadow-dark);
    }
}

body.light {
    .news__item {
        background: #fff;
        color: #333;
        box-shadow: var(--shadow);
    }
}

.news__item {
    display: block;
    text-decoration: none;
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    border-radius: 10px;
    margin-bottom: 10px;
    transition: 0.3s ease;


    .news__item__img {
        >div {
            width: 100% !important;
            height: 100% !important;
        }

        img {
            border-radius: 10px 10px 0 0;
            width: 100%;
        }
    }

    .news__item__content {
        padding-top: 10px;
        padding: 8px;

        .news__item__title {
            font-size: 16px;
            margin-bottom: 2px;
        }

        p {
            font-size: 14px;
        }

        .news__item__date {
            font-size: 12px;
            color: var(--primary);
        }
    }

    &:hover {
        transform: scale(1.15);
        z-index: 3333;
    }
}

/********* responsive **********/
@media (max-width: 575.98px) {
    .news__item {
        .news__item__content {
            .news__item__title {
                font-size: 11px;
            }

            p {
                font-size: 11px;
            }
        }

    }

}

@media (min-width: 576px) and (max-width: 767.98px) {
    .news__item {
        .news__item__content {
            .news__item__title {
                font-size: 11px;
            }

            p {
                font-size: 11px;
            }
        }

    }
}

