.banner-section {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    z-index: 0;
    color: #fff;

    .owl-dots {
        position: absolute;
        right: 30px;
        bottom: 20px;
    }

    .owl-theme .owl-nav.disabled+.owl-dots {
        margin-top: 10px;
        position: absolute;
        right: 90px;
        bottom: 15px;
    }

    .owl-theme .owl-dots .owl-dot span {
        width: 12px;
        height: 12px;
        margin: 5px 7px;
        background: #fff;
    }

    .owl-theme .owl-dots .owl-dot.active span,
    .owl-theme .owl-dots .owl-dot:hover span {
        background: var(--primary);
    }
}


.banner {
    height: 40vw;
    max-height: 1012px;
    box-sizing: border-box;
    background: center center / cover no-repeat;
    border-radius: 0 0 50px 50px;
    background-image: url('../../../assets/images/bg.jpg');


    .banner-container {
        display: flex;
        align-items: center;
        justify-content: center;
        height: inherit;
    }
}

.banner-side-image {
    img {
        border: 2px solid #111;
        border-radius: 10px;
    }
}

.banner-title {
    font-weight: 900;
    font-size: 50px;
}

.labels {
    display: flex;
    padding-top: 10px;

    .label-item {
        margin-right: 25px;
        position: relative;
        padding-left: 15px;

        &::before {
            content: '';
            position: absolute;
            left: 6px;
            top: 50%;
            transform: translate(-50%, -50%);
            height: 7px;
            width: 7px;
            border-radius: 50%;
            background-color: #fff;
        }
    }
}

.description {
    padding: 15px 0;
    width: 60%;
}

.info {
    display: flex;

    .info-item {
        margin-right: 50px;

        svg {
            font-size: 21px;
        }
    }

    .vip-button {
        display: block;
        width: 60px;
        height: 25px;
        border-radius: 4px;
        background: var(--primary);
        line-height: 26px;
        font-weight: 600;
        border: none;
        color: #fff;
        font-size: 13px;
        transition: all 0.3s;

        &:hover {
            transform: scale(1.08);
        }

        svg {
            font-size: 15px;
            margin-top: -3px;
        }
    }
}

.slider-buttons {
    padding: 15px 0;
    padding-top: 35px;
    display: flex;


}

.slider-button {
    padding: 0 20px;
    height: 40px;
    border-radius: 25px;
    background-color: var(--primary);
    color: #fff !important;
    text-align: center;
    font-weight: 600;
    line-height: 40px;
    border: none;
    transition: all 0.3s;

    &:hover {
        transform: scale(1.08);
    }


    svg {
        margin-right: 5px;
    }
}

/********** Responsive ***********/
@media (max-width: 575.98px) {
    .hide-element {
        display: none;
    }

    .banner {
        height: 102vw;
        border-radius: 0 0 20px 20px;
        padding-top: 15px;

        .banner-title {
            font-size: 25px;
        }

        .label-item {
            margin-right: 10px;
            font-size: 13px;
        }

        .info {
            padding-top: 5px;

            .info-item {
                margin-right: 10px;
                font-size: 14px;
            }

        }

        .description {
            display: none;
        }

        .slider-buttons {
            display: none;
        }

        .banner-container {
            align-items: end;
            padding-bottom: 30px;
            justify-content: start;
        }
    }

    .banner-section {
        .owl-theme {
            .owl-dots {
                position: absolute;
                right: 13px !important;
                bottom: 5px !important;

                .owl-dot span {
                    width: 10px !important;
                    height: 10px !important;
                    margin: 5px 3px;
                }
            }
        }

    }

    .info .info-item svg {
        font-size: 17px;
    }

    .slider-button {
        padding: 0 6px;
        height: 26px;
        border-radius: 5px;
        font-size: 12px;
        margin-bottom: 10px;
        line-height: 26px;
    }

}

