        .description {
            display: none;
        }

        .slider-buttons {
            display: none;
        }

    }

    .banner-section {
        .owl-theme {
            .owl-dots {
                position: absolute;
                right: 13px !important;
                bottom: 5px !important;

                .owl-dot span {
                    width: 10px !important;
                    height: 10px !important;
                    margin: 5px 3px;
                }
            }
        }
    }

    .banner {
        .banner-container {
            align-items: end;
            padding-bottom: 30px;
            justify-content: start;
        }
    }

    .slider-button {
        padding: 0 6px;
        height: 26px;
        border-radius: 5px;
        font-size: 12px;
        margin-bottom: 10px;
        line-height: 26px;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .banner {
        height: 80vw;
    }

    .description {
        width: 90%;
    }
}

