.title__seeAll {
    display: block;
    position: relative;
    top: 0;
    transition: all .3s;
    border-radius: 5px;
    font-size: 13px;
    width: 55px;
    height: 25px;
    line-height: 25px;
    text-align: center;
}



body.dark {
    .title__seeAll {
        background-color: var(--primary);
    }
}

body.light {
    .title__seeAll {
        background-color: var(--secondary);
        color: #fff;

        &:hover {
            background-color: var(--primary);
        }
    }
}

.title {
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.title__name::before {
    content: "";
    height: -webkit-fill-available;
    width: 7px;
    background: red;
    top: -1px;
    left: 0;
    position: absolute;
    min-height: 18px;
}

.title__name {
    font-weight: 700;
    position: relative;
    padding-left: 15px;
    flex-grow: 0;
    text-transform: uppercase;
}

.title__line {
    margin: 0 15px;
    flex-grow: 3;
}

.title__right {
    display: flex;
    align-items: center;
    flex-grow: 0;
}

/*** Search ***/
.search__section {
    position: relative;
}

.search-box {
    width: 100%;
    border: 2px solid #2d2d2d;
    border-radius: 6px;
    display: flex;
}

.search-form {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.search-form input {
    width: 100%;
    border: 0px;
    background: none;
    font-size: 14px;
    padding-left: 5px;
}

.search-form input:focus {
    outline: none;
    border: none;
}

.search-form button {
    border: none;
    outline: none;
    display: inline-block;
    padding: 0px 7px;
    color: #fff;
    font-size: 22px;
    background: #2d2d2d;
}

/*** title btn ***/
.title__btn {
    background: var(--secondary);
    color: #fff !important;
    padding: 7px 9px;
    border-radius: 5px;
    text-transform: uppercase;
    font-size: 14px;
    display: block;
    margin-left: 9px;
    text-align: center;
}

.title__button-1 {
    flex-basis: 55%;
}

.title__buttons-2 {
    display: flex;
}

.title__btn:hover {
    color: #fff;
    background: var(--primary);
}

/********* responsive **********/
@media (max-width: 575.98px) {
    .title {
        justify-content: space-between;
        margin-top: 10px !important;
    }

    .title__seeAll {
        right: -7px;
    }

    .title__name {
        font-size: 16px;
        padding-left: 12px;
    }


    .title__line {
        display: none;
    }

    .title__right {
        margin-top: 5px;
    }

    .search-box {
        height: 35px;
    }

    .title__buttons-2 .title__btn {
        padding: 7px 9px;
        font-size: 12px;
    }

    .title__buttons-1 .title__btn {
        padding: 7px 14px;
        font-size: 12px;
        flex-basis: 35%;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .title__buttons-2 .title__btn {
        width: 100px;
    }

    .title {
        justify-content: space-between;
        margin-top: 10px !important;
    }

    .title__btn {
        background: var(--secondary);
        color: #fff;
        padding: 7px 0px;
    }
}

