    color: #fff;
}

.infoItem h5 {
    margin-bottom: 0;
}

.infoItem span {
    font-size: 14px;
}

.wallet__ref h6 {
    padding-right: 5px;
    padding-top: 5px;
}

.wallet__ref input {
    width: 50%;
}

.wallet__table th {
    padding-right: 20px;
    min-width: 120px;
}

.wallet__table td {
    padding-right: 20px;
