/******** Dashboard ******/
.current__package {
    width: 100%;
    text-align: center;
}

/****** Subscription ******/
.subscriptions {
    padding-top: 10px;
}

.subscriptions table {
    width: 100%;
}

.subscriptions table tr {
    height: 33px;
}

.subscriptions table tbody td {
    padding-left: 0px;
}

.subscriptions table th,
td {
    min-width: 120px;
}

.show__btn {
    height: 28px;
    width: 28px;
    padding: 6px;
    background: var(--secondary);
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    margin-right: 10px;
    transition: all 0.3s;
}

.show__btn:hover {
    background: var(--primary);
}

.delete__btn {
    height: 28px;
    width: 28px;
    padding: 6px;
    background: var(--primary);
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    margin-right: 10px;
    transition: all 0.3s;
}

.delete__btn:hover {
    background: var(--secondary);
}

.input__end {
    max-width: 300px !important;
    margin-left: auto;
}

/******** Copy form other file ******/
.sign__form {
    background-color: #fff;
    border-radius: 5px;
    padding: 25px;
    margin-top: 25px;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.sign__form__des {
    border-bottom: 1px solid #e2e2e2;
    padding-bottom: 4px;
}

.sign__form a {
    text-decoration: none;
}

.sign__form label {
    font-size: 14px;
}

.sign__form__btn {
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    border: 1px solid #059c47;
    background: #059c47;
    width: 140px;
    display: block;
    transition: 0.3s ease-in;
