}

.form-control:focus {
    box-shadow: none;
}

.loginPopup__loginBtn,
.loginPopup__loginWithFbBtn,
.loginPopup__loginWithGoogleBtn {
    color: #fff;
    /* width: 100%; */
    /* display: block; */
}

.loginPopup__loginBtn:hover,
.loginPopup__loginWithFbBtn:hover,
.loginPopup__loginWithGoogleBtn:hover {
    color: #fff;
}

.loginPopup__loginBtn {
    background: #eb5e0b;
}

.loginPopup__loginWithFbBtn {
    background: #3b5998;
}

.loginPopup__loginWithGoogleBtn {
    background: #ec4646;
}

/******* Auth ***********/
.form-section {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.form-section .nav-tabs {
    border: 0;
    margin: 0;
    padding: 0;
}

.form-section .nav-tabs .nav-link {
    border: 0;
    margin: 0;
    padding: 10px 0;
    width: 50%;
    text-align: center;
    border-radius: 0;
    color: #fff;
}

.form-section .nav-tabs .nav-link {
    background: #143250;
    cursor: pointer;
}

.form-section .nav-tabs .nav-link.active {
    background: var(--primary);
    color: #fff;
}

.form-container {
    background: #fff;
    -webkit-box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
}

.form-input-wrapper {
    padding: 25px;
}

.input-item {
    margin-bottom: 15px;
}

h1.login-header {
    text-align: center;
    padding-bottom: 15px;
    font-size: 30px;
    font-weight: 600;
}

.input-label {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 5px;
    display: block;
    text-align: left;
}

.input-field {
    display: flex;
    padding: 5px;
}

.input-field input {
    border: none;
    outline: none;
    width: 100%;
    padding-left: 8px;
    font-size: 16px;
    color: #777;
}

.input-field {
    border-bottom: 1px solid gainsboro !important;
}

.input-icon {
    color: gray;
    font-size: 16px;
}

.input-field input::placeholder {
    font-size: 16px;
}

.forgot-password {
    display: block;
    text-align: right;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
}

.forgot-password:hover {
    color: var(--primary);
}

.btn-style {
    margin: 25px 0;
    text-align: center;
}

.login-btn {
    background: linear-gradient(45deg, #143250, #39b54a);
    outline: none;
    border: none;
    color: #fff;
    padding: 10px 0;
    width: 50%;
    border-radius: 5px;
    font-size: 16px;
    text-transform: uppercase;
    cursor: pointer;
}

.login-with span:nth-child(1) {
    display: block;
    padding-bottom: 15px;
}

.login-with {
    text-align: center;
}

.auth-icon {
    color: #fff;
    border-radius: 50%;
    display: inline-block;
    font-size: 17px;
    margin: 0 5px;
    cursor: pointer;
    padding: 10px 13px;
}

.have-account {
    padding-top: 15px;
    padding-bottom: 10px;
    text-align: center;
}

.have-account span {
    margin-bottom: 5px;
    font-weight: 500;
    color: #777;
    margin-right: 6px;
}

/************** Contact Form ***********/

.form-title h1 {
    font-weight: 700;
}

.contact-form {
    background: #fff;
    margin-top: 25px;
}

.contact-form .form-control:focus {
    box-shadow: none;
}

/******** profile  **********/
.info__title {
    margin-bottom: 15px;
}

body.dark .profile-menu {
    background: #21232b;
}

body.light .profile-menu {
    background: #fff;
}

body.dark .profile__routes__content {
    background: #21232b;
}

body.light .profile__routes__content {
    background: #fff;
}

.profile-menu {
    padding-bottom: 40px;
    border-radius: 10px;
}

.profile__routes__content {

    padding-bottom: 25px;
    border-radius: 10px;
}

.profile-menu-item {
    font-size: 20px;
    padding: 7px 0;
    padding-left: 14px;
    margin-bottom: 2px;
    display: block;
}

body.dark .profile-active {
    color: #fff !important;
}

.profile-active {
    color: #222 !important;
    border-left: 4px solid var(--primary);
    background: #3c5e531a;
    padding-left: 10px;
}

.profile-menu-item label {
    cursor: pointer;
}

.profile-menu-item input {
    display: none;
}

.profile-icon {
    font-size: 21px;
    margin-right: 7px;
}

.profile {
    text-align: center;
}

.profile-picture {
    max-width: 160px;
    max-height: 200px;
    border-radius: 50%;
    border: 8px solid #39b54a;
}

.profile__user {
    text-align: center;
    padding: 30px 0;
}

.profile__user__img {
    width: 125px;
    height: 125px;
    border-radius: 50%;
    margin: auto;
}

.profile__user__name {
    font-weight: 600;
    padding: 5px 0;
    font-size: 23px;
    margin-top: 5px;
}

.profile__check__success {
    color: green;
}

.profile__btn {
    font-size: 14px;
    font-weight: 600;
    background: #39b54a;
    color: #fff;
    cursor: pointer;
    border: 1px solid #39b54a;
    padding: 4px 0px;
    width: 140px;
    display: block;
    border-radius: 15px;
    -webkit-transition: 0.3s ease-in;
    -moz-transition: 0.3s ease-in;
    -o-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
    text-align: center;
    margin: auto;
    padding: 6px 0;
}

.profile-route {
    font-weight: 600;
    font-size: 15px;
}

/***** Information *******/
.information {}

.information__title {
    border-bottom: 1px solid #e0e0e0;
    margin-bottom: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.information__title h3 {
    font-weight: 600;
    font-size: 23px;
    margin-bottom: 0;
}

.information__body {
    padding-top: 15px;
}

.information__body_item {
    display: flex;
    font-size: 14px;
    margin-bottom: 5px;
}

.information__body_item p:nth-child(1) {
    font-weight: 600;
    width: 20%;
    font-size: 14px;
    padding-right: 15px;
}

.order__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.checkout__order__item {
    border-radius: 12px;
    padding: 15px;
    margin-bottom: 12px;
}

.checkout__order__content h4 {
    font-size: 15px;
    font-weight: 600;
    margin-bottom: 13px;
}

.checkout__order__img {
    width: 80px;
    height: 80px;
}

.checkout__order__date {
    display: flex;
    justify-content: space-between;
    margin-bottom: 11px;
}

/********* input__group **************/
.basic__information__form {
    width: 100%;
}

.basic__information {
    display: flex;
    padding: 40px;
}

.info__title h4 {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    border-bottom: 1px solid #d8d8d8;
    padding-bottom: 4px;
    position: relative;
}

.info__title h4::after {
    content: '';
    position: absolute;
    left: 0;
    height: 3px;
    background: #d8d8d8;
    width: 108px;
    top: 18px;
}

.basic__information__icon {
    background: #333;
    color: #fff;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 19px;
    margin-right: 20px;
}

.input__group {
    padding-bottom: 15px;
}

.input__group input {
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    display: block;
    width: 100%;
    border: 1px solid #e2e8f0;
    font-size: 14px;
    color: #000 !important;
    padding-left: 11px;
}

.input__group input::placeholder {}

.input__group input:focus {
    border: 1px solid #e2e8f0;
    outline: none;
}

.input__select__group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #e2e8f0;
    padding: 2px 10px;
}

.appearance-none {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.input__select {
    border: none;
    width: 100%;
    outline: none;
    font-size: 14px;
}

.input__select:focus {
    outline: none;
    border: none;
}

.input__group label {
    margin-bottom: 0.25rem;
    font-size: 13px;
    display: block;
    text-align: left;
}

.cancel__btn {
    --text-opacity: 1;
    color: #fff;
    color: rgba(255, 255, 255, var(--text-opacity));
    margin-right: 1rem;
    background-color: #718096;
    border: none;
    padding: 5px 11px;
    font-weight: 500;
    font-size: 16px;
}

.submit__btn {
    --text-opacity: 1;
    color: #fff;
    color: rgba(255, 255, 255, var(--text-opacity));
    margin-right: 1rem;
    background-color: #000;
    border: none;
    padding: 5px 11px;
    font-weight: 500;
    font-size: 16px;
}

.transaction__item {
    border: 1px solid #d1d0d0;
    padding: 5px;
    border-radius: 6px;
    min-height: 100px;
    padding-top: 15px;
}

.tra__money {
    display: flex;
    align-items: center;
}

.tran__account {
    padding-left: 31px;
}

.tra__money p {
    margin: 0;
}

.changePassword .input__group {
    padding-bottom: 0;
}

/********* Ticket *******/
.checkout__order__item {
    border-radius: 12px;
    padding: 15px;
    margin-bottom: 12px;
}

.checkout__order__content h4 {
    font-size: 15px;
    font-weight: 600;
    margin-bottom: 13px;
}

.checkout__order__img {
    width: 80px;
    height: 80px;
}

.checkout__order__content__item-edit {
    display: flex;
    font-size: 13px;
}

.checkout__order__content__item-edit p {
    font-weight: 600;
    margin-right: 8px;
}

.checkout__order__date {
    display: flex;
    justify-content: space-between;
    margin-bottom: 11px;
}

.checkout__section {
    background-color: #f7f8fa;
    padding-bottom: 50px;
}

.checkout__order__section {
    padding: 30px 20px;
    color: #333;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 5px 0 #0000000d;
}

.checkout__order {
    display: flex;
    margin-bottom: 12px;
}

.checkout__order__content {
    margin-left: 20px;
}

.checkout__order__content__item {
    display: flex;
    line-height: 11px;
}

.checkout__order__content__item p:nth-child(1) {
    width: 15%;
    font-weight: 600;
}

.checkout__order__content h4 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 15px;
}

/********** Access Code ********/
.content__center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.bg__section {
    position: relative;
    /* width: 300px; */
}

.bg__dark {
    height: 178px;
    /* width: 300px; */
}

.access__code {
    text-align: center;
    padding-top: 25px;
}

.access__code p {
    text-align: center;
}

.event__content {
    color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
    text-align: center;
    padding: 4px;
}

.token__code {
    padding: 5px;
    border-radius: 5px;
    background: #fff;
    color: var(--primary);
    text-transform: uppercase;
    width: 120px;
    margin: auto;
    margin-top: 5px;
}

.forget__token {
    font-size: 13px;
    padding-bottom: 5px;
}

.forget__token a {
    color: var(--primary);
    text-decoration: underline;
}

/********* Responsive *********/

@media (max-width: 991.98px) {
    .information__body_item p:nth-child(1) {
        width: 40%;
    }

    .subscriptions {
        overflow-x: scroll;
    }

    .profile__user__img {
        width: 100px;
        height: 100px;
    }

    .profile__user__name {
        font-size: 15px;
    }

    .profile__btn {
        font-size: 12px;
        padding: 4px 0;
        width: 112px;
    }

    .basic__information {
        display: flex;
        padding: 20px;
    }
}

@media (max-width: 575.98px) {
    .information__body_item p:nth-child(1) {
        font-weight: 600;
        font-size: 13px;
        text-align: left;
        padding-right: 0;
        width: 40%;
    }

    .basic__information {
        display: flex;
        padding: 15px;
    }

    .subscriptions {
        overflow-x: scroll;
    }
}