body.dark {
    .footer {
        background: rgb(10, 12, 15);
        border-top: 1px solid #6e6b6b;
    }

}

body.light {
    .footer {
        background: #f3f3f3;
        border-top: 1px solid #b2b1b1;

        .footer__link__wrapper {
            .footer__link {
                color: #333;
            }
        }
    }
}

.footer {
    padding: 20px 0;
    margin-top: 50px;

    .footer__info {
        text-align: center;

        .footer-image {
            width: 120px;
            margin: auto;

            img {
                padding-bottom: 15px;
            }
        }

        p {
            font-size: 13px;
        }
    }
}




/****** footer links *******/
.footer__link__wrapper {
    padding-top: 15px;

    a {
        color: #333;
    }
}

.footer__link {
    display: block;
    margin-bottom: 10px;
    font-size: 15px;
    font-weight: 600;
    transition: all 0.3s;
    position: relative;

    &:hover {
        color: var(--primary) !important;
        transform: translateX(10px);
        padding-right: 18px;

        &::after {
            content: "";
            position: absolute;
            width: 12px;
            height: 2px;
            background: var(--primary);
            top: 45%;
            left: -15px;
        }
    }

}


.footer__social__wrapper {
    display: flex;
    flex-wrap: wrap;
}

.footer__social__icon {
    margin-right: 10px;
    transition: all 0.3s;
    height: 35px;
    width: 35px;
    background: var(--secondary);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    border-radius: 50%;
    color: #fff !important;
    margin-top: 7px;

    &:hover {
        background-color: var(--primary);
        color: #fff;
        transform: scale(1.15);
    }

}

.footer__link__title {
    font-size: 25px;
}

.footer__links {
    padding-left: 35px;
    padding-top: 70px;
}

.footer__social {
    padding-top: 70px;
}

/************ Responsive *************/
@media (max-width: 575.98px) {
    .footer {
        padding-bottom: 85px;
    }

    .footer__links {
        padding-left: 0;
        padding-top: 20px;
    }

    .footer__social {
        padding-top: 0;
        text-align: center;

        .footer__social__wrapper {
            justify-content: center;
        }
    }

    .footer__link__wrapper {
        padding-top: 0px;
    }

    .footer__link {
        margin-bottom: 1px;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .footer__link__title {
        font-size: 17px;
    }

    .footer {
        padding-bottom: 85px;
    }

    .footer {
        padding-bottom: 85px;
    }

    .footer__links {
        padding-left: 0;
        padding-top: 25px;
    }


    .footer__link__wrapper {
        padding-top: 0px;
    }

    .footer__link {
        margin-bottom: 1px;
    }

    .footer__social {
        padding-top: 0;
        text-align: center;

        .footer__social__wrapper {
            justify-content: center;
        }
    }

}

@media (min-width: 768px) and (max-width: 991.98px) {
    .footer__link__title {
        font-size: 20px;
    }

    .footer {
        padding-bottom: 85px;
    }

    .footer__link {
        margin-bottom: 3px;
    }

    .footer__social {
        padding-top: 10px;
        text-align: center;

        .footer__social__wrapper {
            justify-content: center;
        }
    }
}

@media (min-width: 992px) and (max-width: 1199.98px) {}