.FindTokenWrapper {
    padding-bottom: 35px;
}
.find__token__form {
    padding: 40px 30px;
    box-shadow: 1px 1px 5px #000;
    border-radius: 10px;
    margin-left: 50px;
}

/******* Responsive ********/
@media (max-width: 575.98px) {
    .find__token__form {
        margin-left: 0;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .find__token__form {
        margin-left: 0;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .find__token__form {
        margin-left: 0;
    }
}

