.add-margin {
    padding-top: 60px;
}



.height {
    height: 250px;
}

/***** previous *******/
.custom__form__group {
    margin-bottom: 15px;
}



.custom__submit__btn {
    border: none;
    color: #fff;
    padding: 5px 15px;
    background: var(--secondary);
    border-radius: 5px;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 500;
    transition: all 0.3s;
}

.custom__submit__btn:hover {
    background: var(--primary);
}

/****** Custom slider container *****/
.custom__slider__container .slick-slider.slick-initialized {
    margin-left: -10px;
}


.slick-next:before,
.slick-prev:before {
    font-size: 26px;
    line-height: 1;
    opacity: 1;
    color: #ED1B24;
    z-index: 100;
}

/*********/

.custom__form__group {
    margin-bottom: 15px;
}

.custom__form__label {
    font-size: 14px;
    font-weight: 700;
    display: block;
    margin-bottom: 5px;
}

.custom__form__input {
    border: 2px solid var(--secondary);
    border-radius: 6px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 15px;
    width: 100%;
    padding: 5px 5px;
    font-weight: 500;

    &:focus {
        border-radius: 6px;
        outline: none;
        border-color: var(--primary);
    }
}

.form-control {
    &:focus {
        border-color: var(--primary) !important;
    }
}

.custom__btn {
    border: none;
    color: #fff;
    background: var(--secondary);
    border-radius: 5px;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 500;
    transition: all 0.3s;
    height: 38px;
    line-height: 38px;
    padding: 0 10px;

    svg {
        font-size: 20px;
        margin-top: -3px;
    }

    &:hover {
        background: var(--primary);
    }
}


.follow__icon {
    margin-right: 5px;
    font-size: 22px;
    color: var(--secondary);
    transition: all 0.3s;
}

.follow__icon:hover {
    color: var(--primary);
}

.ReactModal__Overlay.ReactModal__Overlay--after-open {
    z-index: 9999;
    background-color: #292929bf !important;
}

.ReactModal__Content {
    &::-webkit-scrollbar {
        display: none;
    }
}





body.dark {
    .contact__img {
        img {
            background: #21232b;
            box-shadow: var(--shadow-dark);
        }
    }

    .box-shadow {
        box-shadow: var(--shadow-dark);
    }

    .contact__form__wrapper {
        box-shadow: var(--shadow-dark);
        background: #21232b;
    }
}

body.light {
    .contact__img {
        img {
            background: #fff;
            box-shadow: var(--shadow);
        }
    }

    .box-shadow {
        box-shadow: var(--shadow);
    }

    .contact__form__wrapper {
        box-shadow: var(--shadow);
        background: #fff;
    }
}

.contact__form__wrapper {
    border-radius: 10px;
    height: 100%;
    padding: 25px 20px;
}


.contact__img {
    img {
        border-radius: 10px;
        width: 100%;
    }
}


