/********* Profile ***********/
.profile {
    .profile__img {
        text-align: center;

        div {
            background-color: transparent !important;
        }

        img {
            width: 180px !important;
            border: 4px solid var(--primary);
            border-radius: 50%;
            transition: all 0.3s;
        }
    }

    &:hover {
        .profile__img {
            img {
                transform: scale(1.06);
            }
        }

        .profile__link {
            color: #fff;
            background: var(--primary);
        }

    }
}


.profile__link {
    display: block;
    color: #fff !important;
    background: var(--secondary);
    padding: 6px 15px;
    font-size: 15px;
    font-weight: 600;
    border-radius: 20px;
    width: 142px;
    margin: auto;
    margin-top: -29px;
    position: relative;
    transition: all 0.3s;

    &:hover {
        color: #fff;
        background: var(--primary);
    }
}



.profile__info {
    text-align: center;
    padding-top: 15px;
}

.profile__info h5 {
    font-size: 17px;
}

.heart__icon {
    color: var(--primary);
    margin-right: 5px;
    cursor: pointer;
}

/*********** Profile Banner ***********/
.profile__banner {
    position: relative;

    .owl-theme {
        .owl-nav {

            .owl-prev,
            .owl-next {
                background: transparent !important;
                font-size: 35px !important;
                display: none;
            }

            .owl-prev {
                position: absolute;
                top: 50%;
                left: 0;
                transform: translate(0%, -50%);
            }

            .owl-next {
                position: absolute;
                top: 50%;
                right: 0;
                transform: translate(-35%, -50%);

            }
        }
    }

    &:hover {
        .owl-theme {
            .owl-nav {

                .owl-prev,
                .owl-next {

                    display: block;
                }
            }
        }
    }
}

/********** Profile Details **********/
.profile__details__wrapper {
    box-shadow: 1px 1px 5px #000;
    padding: 20px;
    border-radius: 10px;
}

.profile__banner {
    height: 320px;
    border: 2px solid var(--secondary);
    border-radius: 10px;
    position: relative;
    margin-bottom: 35px;
}

.profile__banner__img {
    height: 320px;
    overflow: hidden;
    width: 100%;
    border-radius: 7px;
}

.profile__image {
    width: 150px;
    height: 150px;
    position: absolute;
    left: -10px;
    bottom: -60px;
    z-index: 3;

    >div {
        background: transparent !important;
    }
}

.profile__image img {
    border: 3px solid var(--secondary);
    border-radius: 50%;
}

.profile__heart__icon {
    bottom: -25px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 30px;
    color: var(--primary);
    cursor: pointer;
    transition: all 0.3s;
}

.profile__heart__icon:hover {
    font-size: 31px;
}

.profile__social {
    position: absolute;
    right: 10px;
    bottom: 10px;
    display: flex;
    align-items: center;
    z-index: 33;
}

.profile__social__icon {
    width: 33px;
    height: 33px;
    border-radius: 50%;
    background: var(--secondary);
    color: #fff !important;
    margin-right: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile__social__icon:hover {
    color: var(--primary);
}

/********* Responsive ***********/
@media (max-width: 575.98px) {
    .profile {
        .profile__img {
            img {
                width: 120px !important;
            }
        }

        &:hover {
            .profile__img {
                img {
                    transform: scale(1);
                }
            }
        }

    }

    .profile__link {
        width: 103px;
        font-size: 12px;
        padding: 3px 15px;
        margin-top: -23px;
    }

    .profile__info h5 {
        font-size: 13px;
    }

    .profile__banner__img {
        height: 130px;
    }

    .profile__banner {
        height: 130px;
    }

    .profile__image {
        width: 70px;
        height: 70px;
    }

    .profile__social .custom__submit__btn {
        font-size: 12px;
        padding: 5px 5px;
    }

    .profile__social__icon {
        width: 26px;
        height: 26px;
        font-size: 13px;
        margin-right: 3px;
    }

    .profile__details__wrapper .about__option {
        font-size: 15px;
    }

    .profile__heart__icon {
        bottom: -21px;
        font-size: 25px;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .profile {
        &:hover {
            .profile__img {
                img {
                    transform: scale(1);
                }
            }
        }
    }

    .profile__banner {
        height: 220px;
    }

    .profile__image {
        width: 110px;
        height: 110px;
    }


    .profile__info h5 {
        font-size: 14px;
    }

    .profile__link {
        width: 105px;
        font-size: 12px;
        padding: 3px 0;
        margin-top: -23px;

    }
}

