.sidebar-wrapper {
    position: fixed;
}

.sidebar-header {
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 7px;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 22;

    .times-icon {
        font-size: 21px;
        cursor: pointer;
        color: red;
    }
}

.side-menu {
    padding: 10px;
    position: relative;
    height: 100vh;
    overflow: scroll;
    padding-top: 50px;

    &::-webkit-scrollbar {
        display: none;
    }


    .side-menu-item {
        display: block;
        text-transform: uppercase;
        margin-top: 15px;
        font-size: 15px;
        cursor: pointer;
        color: #fff;

        svg {
            font-size: 23px;
            margin-right: 8px;
            margin-top: -2px;
        }
    }
}


@media (max-width: 575.98px) {
    .category-menu {
        width: 100%;
    }
}