.owl-prev span {
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 43px;
    padding-bottom: 6px;
    border-radius: 50%;
    background: #3322;
    color: var(--primary);
    font-weight: bold;
}

.owl-prev,
.owl-next {
    background: var(--primary) !important;
    height: 25px !important;
    width: 25px !important;
    color: #fff !important;
    line-height: 25px;

    svg {
        margin-top: -2px !important;
    }

    &:hover {
        svg {
            color: #fff !important;
        }
    }
}




.btn-secondary {
    background: var(--secondary) !important;
}

.btn:focus {
    box-shadow: none;
}

.btn {
    box-shadow: none;
}

.dropdown-menu-dark .dropdown-item.active,
.dropdown-menu-dark .dropdown-item:active {
    color: #fff;
    background-color: var(--primary);
}

.navs-right {
    .owl-stage-outer {
        position: relative;
        overflow-x: clip;
        -webkit-transform: translate3d(0px, 0px, 0px);
        overflow-y: visible;
    }

    .owl-theme .owl-dots {
        text-align: center;
        -webkit-tap-highlight-color: transparent;
        position: absolute;
        right: 0;
        bottom: -2px;
        z-index: -1;

        .owl-dot {
            &:hover {
                span {
                    background: #ed1b28;
                }
            }
        }

        .active {
            span {
                background: #ed1b28 !important;
            }
        }
    }
}

.owl-carousel .owl-wrapper,
.owl-carousel .owl-item {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -webkit-transform: none;
    -moz-transform: none;
    -ms-transform: none;
}

/********* responsive **********/
@media (max-width: 575.98px) {
    .navs-right {
        .owl-theme .owl-dots {
            text-align: center;
            -webkit-tap-highlight-color: transparent;
            position: absolute;
            right: 0;
            bottom: -2px;

            .owl-dot {
                span {
                    width: 8px;
                    height: 8px;
                }
            }
        }
    }

    .upComing {
        .owl-theme .owl-dots {
            right: 0;
            bottom: -24px;
        }
    }


    .ReactModal__Content {
        width: 95% !important;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {}

@media (min-width: 768px) and (max-width: 991.98px) {}

@media (min-width: 992px) and (max-width: 1199.98px) {}

@media (min-width: 1200px) {

    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl,
    .container-xxl {
        max-width: 90% !important;
    }
}