.title {
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.event__title.title__name::before {
    content: '';
    height: 20px;
    width: 7px;
    background: red;
    top: 6px;
    left: 0;
    position: absolute;
    display: none;
}
.calender {
    margin-top: -9px;
    margin-right: 4px;
}
.price {
    margin-top: -6px;
    margin-right: 4px;
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .title__btn {
        background: var(--secondary);
        color: #fff;
        padding: 7px 0px;
    }
}

