.header-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #21232b;
    z-index: 333;
    color: #fff;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    transition: height 0.4s ease-in-out;
    height: 60px;
}

.arrow-up {
    text-align: center;
    cursor: pointer;

    svg {

        background: #323232;
        width: 40px;
        border-radius: 5px;

    }
}

.header-bottom-menu {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 5px;

    .menu-item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        color: #fff !important;

        .menu-item-icon {
            display: block;
            font-size: 21px;
        }

        span {
            font-size: 12px;
            font-weight: 600;
        }
    }

    .active {
        color: var(--primary) !important;
    }
}