.form-section {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #c8141b;
    background-repeat: no-repeat;
    background-size: cover;
}

.custom_pading {
    padding-bottom: 50px;
    margin-top: 50px;
}

.form-container {
    border-radius: 10px;
    height: 100%;

}

.form__tabs {
    padding-top: 40px;
    text-align: center;
}

.form__tabs__option {
    font-size: 17px;
    font-weight: 700;
    cursor: pointer;
}

.addBorder {
    border-bottom: 2px solid var(--primary);
}

.or {
    padding: 0px 8px;
    font-size: 12px;
}

.form-input-wrapper {
    padding: 35px;
}

.input-item {
    margin-bottom: 15px;
}

h1.login-header {
    text-align: center;
    padding-bottom: 15px;
    font-size: 30px;
    font-weight: 600;
}

.input-label {
    font-size: 15px;
    font-weight: 600;
    display: block;
}

.input-field {
    display: flex;
    padding: 5px;
}

.input-field input {
    border: none;
    outline: none;
    width: 100%;
    padding-left: 8px;
    font-size: 16px;
    color: var(--secondary);
}


.input-icon {
    font-size: 16px;
    color: gray;
}

.input-field input::placeholder {
    font-size: 16px;
}

.forgot-password {
    display: block;
    text-align: right;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
}

.btn-style {
    margin: 25px 0;
    text-align: center;
}

.have-account {
    padding-top: 15px;
    padding-bottom: 10px;
    text-align: center;

    span {
        margin-bottom: 5px;
        font-weight: 500;
        margin-right: 6px;
    }

    .sign-up {
        text-decoration: underline;
        font-weight: 500;
        color: var(--primary);
        cursor: pointer;
        font-size: 15px;
    }
}


.forget__password {
    text-align: right;
    font-size: 14px;
    display: block;
    color: var(--secondary);
}

.auth__with {
    display: flex;
    justify-content: space-around;
    width: 50%;
    margin: auto;
}

.auth__with img {
    cursor: pointer;
}

.show__password {
    font-size: 21px;
    position: relative;
    top: 6px;
    cursor: pointer;
}

.custom__submit__btn {
    border: none;
    padding: 5px 15px;
    border-radius: 5px;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: 500;
    transition: all 0.3s;
}

.custom__submit__btn:hover {
    background: var(--primary);
}

/*** Contact Form ****/
.form-title h1 {
    font-weight: 700;
}

.contact-form {
    background: #fff;
    margin-top: 25px;
}

.contact-form .form-control:focus {
    box-shadow: none;
}

.auth__with {
    .auth-with-item {
        background: #f0f1f1;
        height: 45px;
        width: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
    }
}

body.dark {
    .contact__form__wrapper {
        .custom__submit__btn {
            background: var(--primary);
        }

        .input-field {
            input {
                background: transparent !important;
                color: #d3d2d2 !important;
            }
        }

        .input-field {
            border-bottom: 1px solid var(--secondary) !important;
        }
    }
}

body.light {
    .contact__form__wrapper {
        .input-field {
            border-bottom: 1px solid #dfdfdf !important
        }
    }
}


@media (max-width: 575.98px) {
    .auth__with {
        width: 80%;
    }

    .form-container {
        margin: 0;
    }

    .form-input-wrapper {
        padding: 15px;
    }

    .form__tabs {
        padding-top: 30px;
        text-align: center;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .form-container {
        margin: 0;
    }

    .form-input-wrapper {
        padding: 25px;
    }
}

