/***** Event Item ********/
body.dark {
    .event__item {
        background: #21232b;
        color: #fff;
        box-shadow: var(--shadow-dark);

        .event__item__btn {
            color: #fff;
        }
    }
}

body.light {
    .event__item {
        background: #fff;
        color: #333;
        box-shadow: var(--shadow);

        .event__item__btn {
            color: #333;
        }
    }
}

.event__item {
    border-radius: 10px;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    z-index: 1;

    .event-title {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 15px;
    }

    .event-artist-name {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: normal;
        display: inline-block;
        text-align: center;
    }

    .event__item__img {
        >div {
            display: block !important;
        }

        img {
            border-radius: 10px 10px 0 0;
            width: 100%;
        }
    }

    .event__item__content {

        border-radius: 0 0 10px 10px;

        .event__item__title {
            background: #ed1b24;
            text-transform: uppercase;
            color: #fff;
            font-weight: 700;
            text-align: center;
            margin: auto;
            position: relative;
            top: -8px;
        }


        .event__item__details {
            width: 90%;
            margin: auto;
            position: relative;

            h6 {
                font-weight: 600;
                margin-bottom: 3px;
                text-align: center;
            }

            span {
                display: block;
                font-size: 12px;
            }
        }

        .event__item__btn {
            padding: 3px 8px;
            margin-bottom: 2px;
            display: inline-block;
            font-weight: 600;
            border-radius: 6px;
            transition: all 0.3s linear;
            font-size: 14px;
            border: none !important;
            background: transparent;
        }


    }

    &:hover {
        transform: scale(1.2);
        transition: 0.3s ease;
        z-index: 3;

        .event__item__btn {
            color: #fff !important;
            border: 2px solid var(--primary);
            background: var(--primary);
        }
    }
}


/********* Responsive *******/

@media (max-width: 575.98px) {
    .event__item {
        &:hover {
            transform: scale(1);
        }


    }

    .event__item__title {
        font-size: 12px;
    }

    .event__item__details {
        h6 {
            font-size: 13px;
        }
    }

    .event__item .event__item__content .event__item__btn {
        padding: 2px 6px;
        font-size: 12px;
        background: var(--primary);
        font-size: 10px;
        color: #fff !important;
    }

    .event__item__img {
        img {
            height: 150px;
        }
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .event__item__img {
        img {
            height: 150px;
        }
    }

    .event__item {
        &:hover {
            transform: scale(1);
        }
    }

    .event__item__title {
        font-size: 12px;
    }

    .event__item .event__item__content .event__item__btn {
        padding: 2px 6px;
        font-size: 12px;
        background: var(--primary);
        font-size: 10px;
        color: #fff !important;
    }
}

