.order__table table {
    width: 100%;
    transition: all 0.3s;
}
.order__table table thead tr td {
    font-weight: 600;
}
.order__table table tbody tr td {
    padding-left: 0;
    font-size: 14px;
}

.order__table table tbody tr:hover {
    background: #f3f3f3;
}
.chart__item {
    margin-bottom: 60px;
}

/**** conversion chart ******/
.conversion__chart {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--secondary);
    padding-bottom: 15px;
}
.conversion__chart__item {
    font-size: 14px;
    margin-right: 20px;
}
.conversion__chart__item span:nth-child(1) {
    font-weight: 600;
    color: var(--primary);
}
.conversion__chart__item span:nth-child(3) {
    font-weight: 600;
}

/********* Grand Total *******/
.grand__total {
    padding-top: 25px;
    /* text-align: right; */
}
.grand__total__item {
    font-size: 14px;
}
.grand__total__item span:nth-child(1) {
    color: var(--primary);
    font-weight: 600;
}
.grand__total__item span:nth-child(3) {
    font-weight: 600;
    margin-left: 5px;
}
