/*** dots ***/

.slick-dots li {
    position: relative;
    display: inline-block;
    padding: 0;
    cursor: pointer;
}
.slick-dots li button:before {
    font-size: 20px;
    width: 15px;
    height: 15px;
    content: '';
    text-align: center;
    opacity: 0.25;
    background: #fff;
    border-radius: 15px;
}

.slick-dots {
    position: absolute;
    bottom: 15px;
}

.slick-active button::before {
    opacity: 1 !important;
    width: 15px !important;
    margin-right: 8px;
}

/** slick **/
.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin-left: 0 !important;
    margin-right: auto;
}

