.toggle__btn {
    font-size: 26px;
    cursor: pointer;
    transition: all 0.5s linear;
    position: fixed;
    right: 10px;
    top: 17px;
    z-index: 333;
    color: yellow;
}

.light__btn {
    color: yellow;
}

.dark__btn {
    color: #fff;
}

.moon__btn {
    font-size: 34px;
    color: #fff;
    cursor: pointer;
    transition: all 0.5s linear;
}

.toggle__btn:hover {
    transform: rotate(90deg);
}

.dark__btn:hover {
    color: yellow;
}

/********* responsive **********/
@media (max-width: 575.98px) {
    .toggle__btn {
        right: 5px;
        top: 12px;
    }
}

