.blog__details__img {
    text-align: center;
    padding-bottom: 15px;
}

.blog__details__title {
    padding: 15px 0;
}

.date {
    color: var(--primary);
    font-size: 14px;
}

.blog__details__option {
    padding-bottom: 25px;
}



/********* responsive **********/
@media (max-width: 575.98px) {
    .blog-title-wrapper {
        .title {
            margin: 0 !important;
            padding-bottom: 15px;

            .title__right {
                margin-top: -9px !important
            }
        }
    }

}

@media (min-width: 576px) and (max-width: 767.98px) {
    .blog-title-wrapper {
        .title {
            margin: 0 !important;
            padding-bottom: 15px;

            .title__right {
                margin-top: -9px !important
            }
        }
    }
}

