@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400;1,500;1,600&display=swap');
@import url('./slick.theme.css');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary: #ed1b28;
    --secondary: #21232b;

    --bg-primary: #ff0000;
    --secondary-color: #0e1726;
    --text-color: #58555e;
    --btn-bg: #3f3981;
    --dark-bg-1: #0e1726;
    --dark-bg-2: #1b2e4b;
}
.modal-content .form-control {
    background: var(--secondary);
    color: #fff;
}
.modal-content .form-control:focus {
    background: var(--secondary);
    color: #fff;
}
.modal-content {
    background-color: #fff;
    color: var(--dark-bg-1);
}



h3 {
    margin: 0;
}
p {
    text-align: justify;
    margin-bottom: 0;
}

a {
    text-decoration: none;
}
.brand__title {
    color: #ff0000;
    font-weight: 600;
}

img {
    max-width: 100%;
    height: auto;
}

.custom__btn {
    border: none;
    width: 250px;
    font-size: 20px;
    padding: 7px 0;
    border-radius: 5px;
    text-align: center;
    transition: all 0.3s;
    background: transparent;
    color: #fff;
    border: 2px solid #ff0000;
}

.custom__btn:hover {
    background: #ff0000;
    color: #fff;
    border: 2px solid #ff0000;
}

.navbar-toggler:focus {
    text-decoration: none;
    outline: 0;
    box-shadow: 0 0 0 0.15rem;
}
/******** Modal ********/
.ReactModal__Overlay.ReactModal__Overlay--after-open {
    z-index: 9999;
    background-color: #292929bf !important;
}
.quick-times-btn {
    position: absolute;
    right: 0;
    top: 0;
    background: #fff;
    opacity: 1;
    width: 25px;
    height: 25px;
    text-align: center;
    border-radius: 50%;
    outline: none;
    border: none;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}
/********** NAV **********/
.nav {
    padding: 1rem ;
    background-color: #141414;
    box-shadow: 1px 0 0 rgb(22 8 43 / 10%);
}

/********* Basic Box ***********/


.form-control:focus {
    box-shadow: none;
}
.form-select {
    height: auto;
    color: #000;
    font-size: 15px;
    padding: 8px 10px;
    letter-spacing: 1px;
    height: calc(1.4em + 1.4rem + 2px);
    padding: 0.75rem 1.25rem;
    border-radius: 6px;
}

.form-check-input[type='checkbox'] {
    border-radius: 0.25em;
    width: 19px;
    height: 19px;
    margin-right: 9px;
    margin-top: 1px;
}
.submit__btn {
    margin: 0 0 4px 4px;
    border-radius: 4px;
    border: 1px solid var(--btn-bg);
    padding: 18px 30px;
    width: 320px;
    background-color: var(--btn-bg);
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    transition: all 0.3s;
}
.submit__btn:hover {
    background-color: var(--primary);
}
.add__mail {
    cursor: pointer;
    text-decoration: underline;
    margin-top: 5px;
    display: block;
}

/******** Checkout Box *************/
.checkout__form {
}
.checkout__form__box {
    padding: 25px;
    background-color: var(--secondary);
    border-radius: 4px;
    color: #fff;
}
.checkout__form__header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 15px;
}
.checkout__action__btn {
    display: flex;
    align-items: flex-start;
    flex-direction: row-reverse;
    position: relative;
}
.action__btn {
    display: inline-block;
    background-color: #3f3981;
    margin: 0 10px 5px 0;
    border: 1px solid #3f3981;
    border-radius: 4px;
    padding: 10px 16px;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
    text-align: center;
    line-height: 18px;
}
.modal-dialog {
    max-width: 680px;
    margin: 1.75rem auto;
}
.modal-content {
    border-radius: 0px;
}
.checkout__field__section {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.checkout__field__element {
    background-color: var(--dark-bg-2);
    border: 1px solid #6b6b6b;
    min-height: 18px;
    padding: 10px;
    margin: 0 0 10px 0;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
}
.checkout__field__element:hover {
    border: 1px solid #abaaaa;
    margin-bottom: 9px;
}

.checkout__icon {
    font-size: 22px;
    cursor: pointer;
}
.checkout__icon-delete {
    margin-left: 15px;
    font-size: 19px;
}

.checkout__icon-delete:hover {
    color: #ff0000;
}

#submit {
    margin: 0 0 4px 4px;
    border-radius: 4px;
    border: 1px solid var(--btn-bg);
    padding: 12px 30px;
    width: 320px;
    background-color: var(--btn-bg);
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
}

#cancel {
    width: auto;
    margin: 0 0 4px 4px;
    border-radius: 4px;
    padding: 12px 16px;
    background-color: #fff;
    border: 1px solid #909090;
    color: #909090;
    font-family: 'Open Sans', 'Helvetica', 'Arial', sans-serif;
    font-size: 16px;
    text-align: center;
}
#cancel:hover {
    text-decoration: underline;
    cursor: pointer;
}

.notification {
    background-color: #ffe2ad;
    color: #1b0702;
    display: flex;
    margin: 20px 0;
    width: 100%;
    padding: 20px 12px 20px 25px;
    box-sizing: border-box;
    border-radius: 4px;
}
.notification__icon svg {
    font-size: 26px;
    margin-right: 12px;
    transform: rotate(-45deg);
}

.notification__bg {
    background-color: #ffe2ad;
    color: #1b0702;
    margin: 20px 0;
    width: 100%;
    padding: 20px 12px 20px 25px;
    box-sizing: border-box;
    border-radius: 4px;
}

.modal.show .modal-dialog {
    transform: none;
    color: #000;
}
.setting__toggler {
    display: none;
}
.toggle__bar {
    font-size: 22px;
    margin-left: 30px;
    margin-right: 10px;
    cursor: pointer;
}
.submit__align {
    text-align: right;
}

@media (max-width: 575.98px) {
}

@media (min-width: 576px) and (max-width: 767.98px) {
}

@media (min-width: 768px) and (max-width: 991.98px) {
}

@media (min-width: 992px) and (max-width: 1199.98px) {
    .container {
        padding-left: 0;
        padding-right: 0;
    }
}

@media (min-width: 1200px) {
    .container {
        padding-left: 0;
        padding-right: 0;
    }
}

@media (max-width: 575.98px) {
    
    .submit__btn {
        padding: 8px 30px;
        width: 240px;
    }
    #cancel {
        padding: 5px 16px;
    }
    #submit {
        padding: 8px 30px;
    }
    .toggle__bar {
        font-size: 22px;
        margin-left: 4px;
    }

    .mobile__toggle__handler {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }
    .setting__toggler {
        display: block;
        font-weight: 600;
    }
    .down__arrow {
        font-size: 22px;
    }
    .display__sm-none {
        display: none;
    }
    .form-check label {
        display: inline;
    }
    .submit__align {
        text-align: center;
    }
}
